define([
    "esri/graphic",
    "esri/geometry/Circle",
    "esri/geometry/Point",
], function (
    Graphic,
    Circle,
    Point,
) {
    /*
    * @description: Objeto que contiene los componentes para la 
       asignación de ubicacion de predios
    */
    const LandAssignment = {
        title: null, // @params
        lands: null, // @params
        pointLots: null, // @params
        landsSymbol: null, // @params
        landsSymbolSelected: null, // @params
        map: null, // @params
        graphicLayer: null, // @params


        removeGraphic(id) {
            const graphic = this.map.graphics.graphics.find(graphic => graphic.attributes && graphic.attributes.id === id && graphic.attributes.type === 'land');
            this.map.graphics.remove(graphic);
        },
        removeAllGraphics() {
            const listGraphics = [...this.map.graphics.graphics];
            for (const graphic of listGraphics) {
                if (graphic.attributes && graphic.attributes.type === 'land') {
                    this.map.graphics.remove(graphic);
                }
            }
        },
        checkPointLotsSelected() {
            for (const land of this.lands) {
                if (!land.pointLot) {
                    return false;
                }
            }
            return true;
        },
        selectedPointLots(evt) {
            const pointLotSelection = evt.target;
            const pointLotSelectionRow = pointLotSelection.parentNode.parentNode;
            // id of land selected
            const landId = pointLotSelectionRow.dataset.id;
            const landCpm = pointLotSelectionRow.dataset.cpm;
            this.removeGraphic(landId);
            // id of point lot selected
            const pointLotId = pointLotSelection.selectedOptions[0].dataset.id;
            // search point lot selected
            const pointLot = this.pointLots.find(pointLot => pointLot.attributes.ID_LOTE === pointLotId);
            // add point lot to land
            this.lands.forEach(land => {
                if (land.id === parseInt(landId)) {
                    land.pointLot = pointLot;
                }
            });

            const buffer = new Circle({
                center: pointLot.geometry,
                radius: 0.5,
                geodesic: true,
                radiusUnit: esri.Units.METERS
            });
            const xmin = buffer.getExtent().xmin;
            const xmax = buffer.getExtent().xmax;
            const ymin = buffer.getExtent().ymin;
            const ymax = buffer.getExtent().ymax;

            const x = Math.random() * (xmax - xmin) + xmin;
            const y = Math.random() * (ymax - ymin) + ymin;

            const landPoint = new Point(x, y, buffer.spatialReference);
            const graphic = new Graphic(landPoint, this.landsSymbol, { id: landId, type: 'land' });
            this.map.graphics.add(graphic);
        },

        highlightLand(evt) {
            const graphicId = evt.currentTarget.dataset.id;
            this.map.graphics.graphics.forEach(g => {
                if (g.attributes && g.attributes.type === 'land') {
                    g.setSymbol(this.landsSymbol);
                }
            });

            const graphic = this.map.graphics.graphics.find(g => {
                return g.attributes && g.attributes.id === graphicId;
            });

            if (graphic) {
                graphic.setSymbol(this.landsSymbolSelected);
            }
        },

        reestartSymbolLand(evt) {
            // const graphicId = evt.currentTarget.dataset.id;
            this.map.graphics.graphics.forEach(g => {
                if (g.attributes && g.attributes.type === 'land') {
                    g.setSymbol(this.landsSymbol);
                }
            });
        },

        stringToObjectHtml(htmlString) {
            const htmlObject = dojo.create('div', { innerHTML: htmlString });
            return htmlObject;
        },

        renderTableLandAssignment() {
            const pointLotsHtml = this.pointLots.map((pointLot, idx) => {
                return `<option data-id=${pointLot.attributes.ID_LOTE}>
                            ${pointLot.attributes.NOM_VIA}
                        </option>`;
            });

            const landsHtml = this.lands.map((land, idx) => {
                return `<tr data-id=${land.id} data-cpm=${land.cpm}>
                            <td>${idx + 1}</td>
                            <td>${land.cpm}</td>
                            <td>${land.address}</td>
                            <td>
                                <select class="pointLotSelectionCm">
                                    <option value="" disabled selected>
                                        ---
                                    </option>
                                    ${pointLotsHtml.join('')}
                                </select>
                            </td>
                        </tr>`;
            });

            const renderString = `<div class="ctnParamsCm">
                        <div class="lblParamCm">
                            <span class="alignVCenter">
                                Graficar predios resultantes de la ${this.title}
                            </span>
                        </div>
                    </div>
                    <div class="ctnParamsCm ctnTablesClsCm">
                        <table id="tableLandsResults" class="tableClsCm">
                            <thead>
                                <tr>
                                    <th class="center-aligned">Nro</th>
                                    <th>Cod. Predio<br>Municipal</th>
                                    <th>Dirección</th>
                                    <th class="center-aligned">Vía</th>
                                </tr>
                            </thead>
                            <tbody>
                                ${landsHtml.join('')}
                            </tbody>
                        </table>
                    </div>`;

            return this.stringToObjectHtml(renderString);
        },

        addRowToTableLandAssignment() {
            const tableBody = document.querySelector('#tableLandsResults tbody');
            const pointLotsHtml = this.pointLots.map((pointLot, idx) => {
                return `<option data-id=${pointLot.attributes.ID_LOTE}>
                            ${pointLot.attributes.NOM_VIA}
                        </option>`;
            });
            this.lands.forEach(land => {
                const rowHtml = `
                    <tr data-id=${land.cpm}>
                        <td>${tableBody.children.length + 1}</td>
                        <td>${land.cpm}</td>
                        <td>${land.address}</td>
                        <td>
                            <select class="pointLotSelectionCm">
                                <option value="" disabled selected>
                                    ---
                                </option>
                                ${pointLotsHtml.join('')}
                            </select>
                        </td>
                    </tr>`;
                tableBody.insertAdjacentHTML('beforeend', rowHtml);
            });
        }
    };
    return LandAssignment;
});